import dianfei from "@/assets/dianfei.png";
import guanlifei from "@/assets/guanlifei.png";
import leixing from "@/assets/leixing.png";
import gonghao from "@/assets/gonghao.png";
import tbfwf from "@/assets/tbfwf.png";
import progressBar from "@/assets/progressBar.png"; //进度条
// import icon_1 from "@/assets/icon_1.png";
// import icon_2 from "@/assets/icon_2.png";
// import icon_3 from "@/assets/icon_3.png";
// import icon_4 from "@/assets/icon_4.png";
// import icon_5 from "@/assets/icon_5.png";
// import icon_6 from "@/assets/icon_6.png";
import loading from "@/assets/loading.gif";
import storage from "../../../common/storage.js";
import CJWT from "./cjwt";
export default {
  data() {
    return {
      loading: loading,
      isShow: true,
      progressBar: progressBar,
      icon5: tbfwf,
      icon1: dianfei,
      icon2: guanlifei,
      icon3: leixing,
      icon4: gonghao,
      // icon_1: icon_1,
      // icon_2: icon_2,
      // icon_3: icon_3,
      // icon_4: icon_4,
      // icon_5: icon_5,
      // icon_6: icon_6,
      topTitle: "产品详情",
      form: {
        goods_id: "",
        back: "",
        goods_name: "",
        stock: "", //库存
        total_num: "",
        start_num: "",
        price: "", //单价
        contract_cycle: "",
        return_cycle: "",
        suitable_user: "",
        power_rate: "",
        service: "",
        mining_model: "",
        actual_power: "",
        coin_type: "",
      },
      tabOne: true,
      tabTwo: false,
    };
  },
  filters: {
    uppercase: function (value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    },
  },
  components: { CJWT },
  mounted() {
    // if (!this.$route.params.id) {
    //   this.$nextTick(function () {
    //     if (storage.get("goods-detail")) {
    //       this.form = storage.get("goods-detail");
    //       this.isShow = false;
    //     }
    //   });
    // } else {
    //   this.getDetails();
    // }
    this.getDetails();
  },
  methods: {
    getDetails() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          goods_id: that.$route.params.id,
        };
      that
        .$request({
          method: "POST",
          url: "/goods/goods-detail",
          data: that.$qs.stringify(formData),
        })
        .then(function (response) {
          console.log("response");
          console.log(response);
          if (response.code == 0) {
            that.isShow = false;
            that.form = response.data;
            that.form.tag = response.data.tag.split(",");
            console.log(response.data);
            storage.set("goods-detail", that.form);
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    submitFun() {
      var that = this;
      // console.log(that.$store.state.user.token);
      if (that.$store.state.user.token) {
        that.$router.push({
          name: "payProduct",
          params: {
            id: that.form.goods_id, //this.$route.params.id //that.$store.state.common.id
          },
        });
      } else {
        that.$router.push({
          name: "login",
        });
      }
    },
    tabItemOne(index) {
      this.tabOne = true;
      this.tabTwo = false;
    },
    tabItemTwo(index) {
      this.tabTwo = true;
      this.tabOne = false;
    },
  },
};



